.footer__container{
    display: grid;
    grid-template-columns: 1fr;
    padding: 3rem ;
    color: var(--text-col2);
}
.footer__container-navs{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
}
.footer__container-navs_logo{
    width: 100%;
    height: 90%;
    overflow: hidden;
   
    padding: 2rem;
   display: inline-flex;
   align-items: center;
   justify-content: center;
}
.footer__container-navs_logo img{
    width: 60%;
   
    
}
.footer__container-navs_links{
    display: flex;
    flex-direction: column;
    width: 100%;
   
    padding: 2rem;
    align-items: center;
}
.footer__container-navs_address{
    width: 100%;
   
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.footer__container-navs_left{
    display: flex;
    flex-direction: column;
    width: 100%;
   
    padding: 2rem;
    align-items: center;
}
.footer__container-navs_left h3{
    padding-bottom: 2rem;
    color: var(--text-col);
    font-size: 2rem;
}
.footer__container-navs_left ul li{
    list-style: none;
padding-bottom: 8px;
font-size: 23px;

}

.footer__container-navs_links ul li{
list-style: none;
padding-bottom: 8px;
font-size: 23px;
transition: 700ms;
}
.footer__container-navs_links ul li:hover{ 
    color: var(--bg-col2);
    background: var(--text-col2);
}
.footer__container-navs_links h3{
    padding-bottom: 2rem;
    color: var(--text-col);
    font-size: 2rem;
}
.footer__container-copyright_icons{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
   gap: 1rem;
   place-items: center;

   padding-bottom: 1rem;
    
}
.footer__container-copyright{
    padding-top: 1rem;
    border-top: 4px solid var(--text-col2);
}
.footer__container-navs_address h3{
    padding-bottom: 2rem;
    color: var(--text-col);
    font-weight: bold;
    font-size: 2rem;

}
.footer__container-navs_address h2{
    font-weight: 100;
    padding-bottom: 5px;
}

.footer__container-copyright_copy{
display: flex;
align-items: center;
justify-content: center;
border: 2px solid var(--text-col2);
color: var(--text-col);
border-radius: 10px;
padding: 1.4rem;
}

.footer__container-copyright_icons div:hover{
color: var(--text-col);

}


.footer__container-copyright_copy h4{
    display: flex;
    flex-direction: row;
}
@media  screen and (max-width:1200px) {
    .footer__container-navs{
        grid-template-columns: 1fr;
        place-items: center;
    }
    .footer__container-navs_logo img{
        width: 100%;
       
        
    }
    .footer__container-navs_links{
        align-items: center;
        justify-content: center;
        border-top: 4px solid var(--text-col);
    }
    .footer__container-navs_address{
        align-items: center;
        justify-content: center;
        border-top: 4px solid var(--text-col);
    }
}

@media  screen and (max-width:600px) {
    .footer__container-copyright_copy h4{
    
        flex-direction: column;
        
     }
     .footer__container-copyright_copy h4 span{
       padding-bottom: .6rem;
         
      }
     
}
