*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body{
  background: var(--bg-col);
}

a{
  color: unset;
  text-decoration: none;
}

.section__padding{
  padding: 4rem 6rem;

}

.section__margin{
  margin: 4rem 6rem;
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

 @-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  

@media screen and (max-width: 700px) {
  .section__margin{
    margin: 3rem;
  }

  .section__padding{
    padding: 3rem;
  }
}

@media screen and (max-width:550px) {
  .section__margin{
    margin: 2rem 2rem;
  }

  .section__padding{
    padding: 2rem 4rem;
  }

}


.gradient__bg{

    /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 3% 25%, rgb(251, 252, 253) 42%, rgb(231, 232, 235) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 3% 25%, rgb(251, 252, 253) 42%, rgb(231, 232, 235) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 3% 25%, rgb(251, 252, 253) 42%, rgb(231, 232, 235)100%);

    /* ie 10+ */
    background:-ms-radial-gradient(circle at 3% 25%, rgb(251, 252, 253) 42%, rgb(231, 232, 235) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 3% 25%, rgb(251, 252, 253) 42%, rgb(231, 232, 235) 100%);

}