.header_head{
height: 20rem;
margin-top:5rem;
overflow: hidden;
font-family: var(--font-typ);
}

.headercard__container{
   width: 100%;
    height: 100%;
    display: grid;
    position: relative;
    place-items: center;
    background: black;
}

.headercard__container-img{
 position: absolute;
 width: 100%;
}

.headercard__container-img img{
   width: 100%;
    opacity: 0.6;
}
.headercard__container-content{
    position: relative;
    width: 44%;
    margin: 0 auto;
    text-align: center;
    color: var(--text-col);
}

.headercard__container-content h2{
    font-size: 4rem;
    color: var(--bg-col);
    padding-bottom:2rem ;
}
.headercard__container-content p{
    font-size: 1.5rem;
    color: var(--bg-col);
}
@media screen and (max-width:600px) {
    .headercard__container-content{
        width: 50%;
    }
}