.missioncard__container{
    font-family: var(--font-typ);
    color: var(--bg-col2);
    display: grid;
    grid-template-columns: 1fr;
    background: var(--text-col2);
    padding: 1rem;
    border-radius: 6%;
    transition: 500ms;
    border:4px solid var(--text-col2);
}
.missioncard__container-image{
    padding-top: 1rem;
    padding-bottom: 2rem;
    border-bottom: 1rem solid var(--bg-col);
    transition: 500ms;
}
.missioncard__container-image img{
    overflow: hidden;
    width: 100%;
    height: 100%;
    transition: 500ms;
}
.missioncard__container-header{
    padding-bottom: 1rem;
    border-bottom: 1rem solid var(--bg-col);
    display: flex;
    justify-content: center;
    align-items: center;
    
   
}

.missioncard__container-header h1{

font-size: 3rem;
}
.missioncard__container-content{
    font-size: 16px;
    padding-bottom: 0.5rem;
    padding-top: 15px;
    width: 100%;
    height: 100%;
    
}


.missioncard__container:hover{
    background: transparent;
    color: var(--text-col2);
    border:4px solid var(--text-col2);
}



@media screen and (max-width:1200px) {
    .missioncard__container-content p{
        font-size: 28px;
    }

    .missioncard__container{
       
    }
}