.services__container-service{
background: var(--bg-col2);
font-family: var(--font-typ);
display: grid;
grid-template-columns: repeat(3, 1fr);
place-items: center;
border-radius: 2rem;
gap: 2rem;
}

.services__container-service_list{
    background: var(--bg-col2);
    border-radius: 2rem;
    transition: 500ms;
    color: var(--text-col2);
    
}

.services__container-service_list h1{
    display: flex;
    align-items: center;
    justify-content: center;
   
    font-family: var(--font-typ);
    font-size: 3rem;
    padding-bottom: 1rem;
    text-transform: uppercase;
    border-bottom: 10px solid var(--bg-col);
}

.services__container-wrapper{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-bottom: 1.5rem;
    padding-top: 1rem;
    gap: 4rem;
    border-bottom: 10px solid var(--bg-col);
}

.services__container-wrapper ul li{
padding: 5px;

font-size: 18px;
text-transform: uppercase;
list-style-type: square;

}

.services__tcs{
display: flex;
justify-content: center;
align-items: center;
color: var(--text-col1);
padding-top: 2rem;
}

.services__container-service_list:hover{
color: var(--bg-col2);
background: var(--text-col2);
padding: 4rem;
}

@media screen and (max-width:1200px) {
   .services__container-service{
    grid-template-columns: 1fr;
    
   }
   .services__container-wrapper{
    grid-template-columns: 1fr;
    
   }
   .services__container-service_list h1{
    font-size: 2rem;
   }
}
@media screen and (max-width:700px) {
    .services__container-service_list h1{
        font-size: 1.7rem;
       } 
}
@media screen and (max-width:570px) {
    .services__container-service_list h1{
        font-size: 1.3rem;
       } 
}