.mission__container{
    font-family: var(--font-typ);
    color: var(--text-col);
   
    background: var(--bg-col2);
    padding: 4rem 4rem;
   
    font-family: var(--font-typ);
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    border-radius: 4rem;
}

@media screen and (max-width:1200px) {
    .mission__container{
        grid-template-columns: repeat(1, 1fr);
        
    }
    .card{
        border-bottom: 2rem solid var(--bg-col);
    }
}

.goal__container{
    font-family: var(--font-typ);
    color: var(--text-col2);
   
    background: var(--bg-col2);
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    border-radius: 4rem;
}
.goal__container-head{
    background: var(--text-col2);
    color: var(--bg-col2);
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    transition: 500ms;
    border: 2px solid var(--text-col2);
}
.goal__container-head h1{
font-size: 21px;
}
.goal__header{
    background: var(--text-col2);
    color: var(--bg-col2);
    display: flex;
    justify-content: center;
    border-radius: 4rem;
}
.goal__header h1{
    font-family: var(--font-typ);
    font-size: 2rem;
}
.goal__container-content_but{
    background: transparent;
    padding: 2px;
    border: 2px solid var(--bg-col)
}

.goal__container-head:hover, .goal__container-content_but:hover{
    background: var(--bg-col2);
    color: var(--text-col2);
    border: 2px solid var(--text-col2);
}

.goal__container-goals{
    padding: 2rem;
}
.goal__container-goals ul li{
    list-style: none;
    margin-bottom: 8px;
    padding: 3px;
    background: var(--bg-col);
    color: var(--text-col2);
    font-family: var(--font-typ);
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

 @-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }

  @media screen and (max-width:1240px) {
    .goal__container{
        
        grid-template-columns: repeat(1, 1fr);
        gap: 2rem;
    }
    .goal__container-goals ul li{
        font-size: 1.5rem;
    }
  }

  @media screen and (max-width:900px) {

    .goal__container-goals ul li{
        font-size: 1.3rem;
        font-weight: bold;
    }
    .goal__container-goals{
        padding:0 ;
        padding-top: 2rem;
    }
    .goal__header h1{
        font-size: 2.7rem;
        font-weight: bold;
    }
    .goal__container-head h1{
        font-size: 23px;
    }
  }
  @media screen and (max-width:900px){
    .goal__container-head h1{
        font-size: 20px;
    }
    .goal__header h1{
        font-size: 2rem;
        font-weight: bold;
    }
  }