.member__container-heading{
    background: var(--bg-col2);
    font-family: var(--font-typ);
    place-items: center;
    border-radius: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--text-col2);

}
.member__container-plans{
    background: var(--bg-col2);
font-family: var(--font-typ);
display: grid;
grid-template-columns: 1fr;
place-items: center;
border-radius: 2rem;
gap: 2rem;
color: var(--text-col2);
}

.member__plans-wrapper{
    display:grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.member__container-plans_individual{
    border:4px solid var(--text-col2);
    font-family: var(--font-typ);
    width: 100%;
    height: 100%;
    padding: 1.5rem;
    border-radius: 1.5rem;
    transition: 800ms;
}
.member__container-plans_individual h1{
    font-size: 2.5rem;
}
.member__container-plans_individual p{
    padding-top: 2rem;
    padding-bottom: 2rem;
    font-size: 1.2rem;
}
.member__container-plans_individual ul{
    padding-left: 1.5rem;
    padding-top: 1rem;
}
.member__container-plans_individual ul li{
padding-bottom: .7rem;
}
.member__container-plans_individual:hover{
    background: var(--text-col2);
    color: var(--bg-col2);
    border:4px solid var(--bg-col);
}
.member__container-plans_schools{
    border:4px solid var(--text-col2);
    font-family: var(--font-typ);
    width: 100%;
    height: 100%;
    padding: 1.5rem;
    border-radius: 1.5rem;
    transition: 800ms;

}
.member__container-plans_schools h1{
    font-size: 2.5rem;
    padding-bottom: 2rem;
}
.member__container-plans_schools ul{
    font-size: 1.2rem;
}
.member__container-plans_schools ul li ul{
    padding: 4px 1.5rem;
}
.member__container-plans_schools:hover{
    background: var(--text-col2);
    color: var(--bg-col2);
    border:4px solid var(--bg-col);
}
@media screen and (max-width:1200px){
    .member__plans-wrapper{
       
        grid-template-columns: 1fr;
       
    }
    .member__container-plans_individual p{
       
        font-size: 1.5rem;
        font-weight: bold;
    }
    .member__container-plans_individual  ul{
        font-weight: 400;
        padding-left: 3rem;
    }
    .member__container-plans_individual h1{
        display: flex;
        justify-content: center;
    }

    .member__container-plans_schools h1{
        display: flex;
        justify-content: center;
    }

    .member__container-plans_schools ul{
        font-size: 1.5rem;
        padding-left: 2rem;
        font-weight: bold;
    }
    .member__container-plans_schools ul li{
        padding-bottom: 5px;
    }
    .member__container-plans_schools ul li ul{
        font-weight: 200;
        text-transform: capitalize;
    }

}

@media screen and (max-width:600px){
    
    .member__container-plans_individual p{
       
        font-size: 1.2rem;
        
    }

  

    .member__container-plans_schools ul{
        font-size: 1.2rem;
      
    }

 

}