.contact__container-info{
    background: var(--bg-col2);
font-family: var(--font-typ);
display: grid;
grid-template-columns: 2fr 1fr;
place-items: center;
border-radius: 2rem;
gap: 2rem;
color: var(--text-col2);
gap: 3rem;
}


.contact__container-map{
    background: var(--bg-col2);
    font-family: var(--font-typ);
    display: grid;
    place-items: center;
    border-radius: 2rem;
gap: 2rem;
color: var(--text-col2);
}
.contact__container-form{
   
    width: 100%;
}
.form__control{
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-left: 2rem;
}

.form__control div{
    width:90%;
    padding: .5rem 0;
    display: flex;
    flex-direction: column;

}
.form__control label{
    font-size: 1.5rem;
    padding-bottom: .4rem;
}
.form__control input{
    width:100%;
    height: 50px;
    border-radius: 10px;
    padding-left: 2rem;
    font-size: 1.5rem;
    border: 3px solid var(--bg-col);
    transition: 700ms;
    
}
.form__control textarea{
    border: 3px solid var(--bg-col);
 border-radius: 10px;
 height: 100px;
 font-size: 1.5rem;
 padding-left:2rem ;
 transition: 700ms;
}

.form__but{
    width: 50%;
    
}
.form__but button{
    background: var(--text-col2);
    padding: 1rem;
    font-size: 2rem;
    color: var(--bg-col2);
    border-radius: 10px;
    border: 3px solid var(--bg-col);
    transition: 700ms;

}
.form__control input:hover{
    border: 3px solid var(--text-col2);
}
.form__control textarea:hover{
    border: 3px solid var(--text-col2);
}

.form__but button:hover{
    background: var(--text-col);
    border: 3px solid var(--text-col2);
}

.contact__container-address{
    display: grid;
    grid-template-columns: 1fr;
    
    width: 100%;
    height: 100%;
}
.contact__container-address h1{
    padding-bottom: 3rem;
}
.contact__container-address h2{
    padding: 0.5rem 1rem;
}
.contact__container-address h3{
    padding: 0.5rem 1rem;
    border-top: 5px solid var(--bg-col);
    border-bottom: 5px solid var(--bg-col);
}
.contact__container-address p{
    padding: 0.8rem 1rem;
    font-size: 1rem;
    font-family: var(--font-typ);
}
.contact__container-address_cta{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 1rem 1rem;
    gap: 20px;
}
.contact__container-address_cta div{
    transition: 700ms;
}
.contact__container-address_cta div:hover{
    color: var(--text-col)
}

@media  screen and (max-width:1200px) {
    .contact__container-info{
        grid-template-columns: 1fr;
    }
    .contact__container-address p{
        font-size: 1.3rem;
    }
    .contact__container-map iframe{
        height: 300;
    }
}
@media  screen and (max-width:550px) {

.contact__container-address_cta{
grid-template-columns: 1fr;
place-items: center;

}
}