
nav{
    display: grid;
    color: var(  --text-col2);
  
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
   z-index: 99;
    justify-content: space-between;
    align-items: center;
}

.navbar__container{
    display: flex;
    width: 100vw;
 padding-top:2rem ;
    padding-bottom: 2rem;
    padding-left: 4rem;
    padding-right: 4rem;
    position: relative;
    justify-content: space-between;
    align-items: center;
}

.navbar__links {
   list-style: none;
   margin: 0;
   padding: 0;
    display: flex;
   
    align-items: center;
    
}

.navbar__links  li{
    margin: 0;
    padding: 4px 0;
    font-weight: bold;
    transition: 700ms ease-in-out;
}
.navbar__links  li:hover{
background:var(--text-col2);
color: var(--bg-col);
font-weight: bolder;
cursor: pointer;


}

.navbar__links li a{
    display: block;
    padding: 0 3rem;
    text-transform: uppercase;
    font-size:16px ;
    font-family: var(--font-typ);
}

.navbar__logo{
    width: 10%;
    overflow: hidden;
}
.navbar__logo img{
    width: 100%;
    height: 100%;
}
.navbar__logo p{
   
font-size: 1.9rem;
color: var(--text-col2);
font-family: var(--font-typ);
font-weight: bolder;

}

.navbar__links p:hover{
    background: var(--bg-col);
    color: var(--head-col);
}


.navbar__menu-container_links{
    line-height: 45px;
    padding-top: 2.5rem;

}
.navbar__menu-container_links p{
    border-bottom: 2px solid var(--text-col);
}

.navbar__menu-container_links p:hover{
    background: var(--bg-col);
    color: var(--head-col);
    font-weight: bolder;
    cursor: pointer;
    padding-right:3px ;
    border-bottom: 2px solid black;
}
/* Responsive Menu */
.navbar__menu{
    display: none;
}


.active_nav{
    position: relative;
}

.active_nav::after{
    content: '';
    display: block;
    width: 1.2rem;
    height: 1.2rem;
    background: var(--text-col2);
    position: absolute;
    left: calc(50% - 0.6rem);
    border-radius: 50%;
    margin-top: 12px;
}




.navbar__menu svg:hover{
    color: var(--head-col);
}

@media screen and (max-width:1200px) {
   
    .navbar__menu{
        display: block;
       
        }

    .navbar__menu-but{
        display:inline-block;
        background: transparent;
        cursor: pointer;
        border: 2px solid var(--text-col2);
    }
    .active_nav, .active_nav::after{
        display: none;
    }
    .navbar__logo p{
   
        font-size: 1.9rem;
    }
    .navbar__links{
        position: absolute;
        top: 100%;
        right: 0;
        flex-direction: column;
        gap: 0;
       }
    .navbar__links li{
        height: 4rem;
        width: 100%;
        box-shadow: -2rem 2rem 5rem rgb(0,0,0,0.4);
    }

    .navbar__links li a {
        background-color: var(--text-col2);
        height: 100%;
        width: 100%;
        display: flex;
        color: var(--bg-col);
        align-items: center;
        padding: 1rem 5rem 1rem 3rem;
    }
    .navbar__links li:hover{
        background: var(--text-col);
        color: var(--bg-col2);
    }

    .show__nav{
        display: flex;
       }
    
       .hide__nav{
        display: none;
       }
       .navbar__logo{
        width: 20%;}
}




@media screen and (max-width:600px) {
    .navbar__logo p{
   
        font-size: 1.2rem;
    }
    .navbar__logo{
        width: 30%;}
}