.about__container{
    font-family: var(--font-typ);
    color: var(--text-col);
    display: grid;
    grid-template-columns: 1fr 1fr;
    background: var(--bg-col2);
    padding: 4rem 4rem;
    gap: 2rem;
    font-family: var(--font-typ);
    border-radius: 4rem;
}

.about__container-image img{
    height: 100%;
    width: 100%;
}

.about__container-text h1{
padding-bottom: 2rem;
font-size: 3rem;
color: var(--text-col2);

}
.about__container-text p{
font-size: 21px;
}


.about__container-image{
    transition: 1000ms;
    border: 5px solid transparent;
    border-radius: 2rem;
    padding: 1rem;
}
.about__container-image:hover{

    border: 5px solid var(--text-col2);
}

@media screen and (max-width: 1200px) {
    .about__container{
        grid-template-columns: 1fr;
    }
    .about__container-image{
        order:1;
    }
}

@media screen and (max-width: 600px) {
    .about__container-text h1{
        font-size: 2.3rem;
    }

    .about__container-text p{
        font-weight: bold;
    }
}