.header__container{
    padding-top: 1rem;
    margin-top: 7rem;
    color: var(--text-col);
    font-family: var(--font-typ);
    display: grid;
    grid-template-columns: 1fr 1fr ;
    padding-bottom: 1rem;
    
    /* box-shadow: 10px 10px 5px rgba(0, 0, 0, 0.3); */

}

.header__container-right img{
    width: 100%;
    height: 100%;
}

.header__left-head h1{
    font-size: 65px;
    font-family: var(--font-typ);
   
    background: var(--text-col);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;

}

.header__left-head{
    padding: 1rem 4rem 4rem;
}
.header__left-para{
    padding-left:4rem;
    padding-right:4rem;
    padding-bottom: 1rem;
}
.header__left-para{
    font-family: var(--font-typ);
    font-size: 20px;
}
.header__left-para p{
    font-size: 22px;
}


.header__left-cta{
    padding: 1rem 4rem 2rem;
}
.header__left-cta button{
    padding: 1rem ;
    font-size: 25px;
    padding-left:3rem ;
    padding-right: 3rem;
    background: var(--text-col2);
    font-weight: bolder;
    font-family: var(--font-typ);
    border-radius: 2%;
    transition: 500ms ease-in-out;
    color: white;
    box-shadow: 10px 10px 5px rgba(0, 0, 0, 0.3);

}

.header__left-cta button:hover{
    background: transparent;
    color: var(--text-col2);
    border-color: var(--text-col2);
}

@media screen and (max-width:1200px) {
    .header__container{
       
        grid-template-columns: 1fr;
    }
    .header__left-para{
        font-size: 30px;
    }
    .header__left-cta button{
        font-size: 35px;
    
    }
    .header__left-cta{
        text-align: center;
    }
    .header__container-left{
        padding-bottom: 2rem;
    }
    .header__container-right img{
        width: 85%;
        height: 100%;
       
    }
    .header__container-right{
        text-align: center;
    }
}

@media screen and (max-width:550px) {
   
    .header__left-para{
        font-size: 28px;
    }
    .header__left-cta button{
        font-size: 25px;
    
    }
  

    .header__container-right img{
        width: 80%;
        height: 100%;
       
    }
    .header__left-head h1{
        font-size: 40px;}
    
}