.service__wrapper{
    display: grid;
    grid-template-columns: 1fr;
    place-items: center;
    background: var(--text-col2);
    width: 100%;
    height: 100%;

    padding: 2rem 2rem;
    border-radius: 2rem;
    transition: 700ms;
}

.service__wrapper-head{
   
    border: 4px solid var(--bg-col);
    padding: 1rem;
    border-radius: 50%;
}
.service__wrapper-content{
    padding-top: 1rem;
    font-size: 18px;
    font-weight: bold;
    color: var(--bg-col);
}

.service__wrapper:hover{
background: var(--text-col);
padding: 1rem;
}


@media screen and (max-width:1200px) {
    .service__wrapper{
        width: 80%;
    }
}
@media screen and (max-width:650px) {
    .service__wrapper{
        width: 100%;
    }
}